<template>
	<div>
		<div class="search">
			<input v-model="keyword" class="search-input" type="text" placeholder="">
		</div>
		<div ref="search" 
		class="search-content"
		v-show="keyword"
		>
			<ul>
				<li 
				class="search-item border-bottom" 
				v-for="item of list" 
				:key="item.id"
				@click="handleCityClick(item.name)"
				>{{item.name}}</li>
				<li class="search-item border-bottom" v-show="hasNoData">
					
				</li>
			</ul>
		</div>
	</div>
</template>
<script >
import {mapMutations} from 'vuex'
import Bscroll from 'better-scroll'
export default{
	name : 'CitySearch',
	props: {
		cities: Object
	},
	data () {
		return {
			keyword:'',
			list:[],
			timer:null
		}
	},
	computed: {
		hasNoData () {
			return !this.list.length
		}
	},
	methods: {
		handleCityClick (city) {
			this.changeCity(city) 
			this.$router.push('/')
		},
		...mapMutations(['changeCity'])
	},
	watch: {
		keyword () {
			if(this.timer){
				clearTimeout(this.timer)
			}
			if(!this.keyword){
				this.list = []
				return
			}
			this.timer = setTimeout(()=>{
				const result = []
				for (let i in this.cities){
					this.cities[i].forEach((value)=>{
						if(value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1){
							result.push(value)
						}
					})
				}
				this.list = result
			}, 100)
		}
	},
	mounted () {
		this.scroll = new Bscroll(this.$refs.search)
	}
}
</script>
<style lang="stylus" scoped>
@import '~styles/varibles.styl'
	.border-bottom
		&:after
			border-color: #ccc
	.search
		height: .72rem
		padding: 0 .1rem
		background: $bgColor
		.search-input
			box-sizing: border-box
			height: .62rem
			width: 100%
			line-height: .62rem
			border-radius: .06rem
			color: #666
			padding: 0 .1rem
	.search-content
		z-index: 1
		overflow: hidden
		position: absolute
		top: 1.58rem
		left: 0
		right: 0
		bottom: 0
		background: #fff
		.search-item
			line-height: .62rem
			padding-left: .2rem
			color: #666
			
		
</style>


